            <col style="width:50%;">
            <col style="width:16%;">
            <col style="width:10%;">
            <col style="width:10%;">
          </colgroup>
          <colgroup  v-if="isMobile">
            <col style="width:60%;">
            <col style="width:20%;">
            <col style="width:20%;">
          </colgroup>
          <tr>
            <th class="m-non t-m-non">선택</th>
            <th class="m-non t-m-non">번호</th>
            <th>제목</th>
            <th>학교명</th>
            <th>작성일</th>
            <th class="m-non t-m-non">조회수</th>
          </tr>
          <tr @click="goToPage('/lms/dataroom/view')" class="link-box">
            <td @click.stop class="m-non t-m-non c-auto"><label class="chk"><input type="checkbox"><i></i></label></td>            
